/* eslint-disable class-methods-use-this */
/* eslint-disable no-unused-vars */
import FramesColor from '../frames-list/framescolor';

const mainCanvas = document.getElementById('main-canvas');
const ctx = mainCanvas.getContext('2d');
let isMouseDown = false;

export default class Dith {
  constructor() {
    this.dithCanvas();
  }

  dithCanvas() {
    mainCanvas.addEventListener('mousedown', () => {
      isMouseDown = true;
    });

    function dithDraw(e) {
      const dithTool = document.getElementById('dithTool');
      if (!dithTool.classList.contains('chosen')) {
        mainCanvas.removeEventListener('mousemove', dithDraw);
      }

      const chosenLine = document.querySelector('.chosen-line');
      const line = window.getComputedStyle(chosenLine).width.slice(0, -2);

      const primaryColorInput = document.getElementById('primary-color');
      const primaryColor = primaryColorInput.value;

      const seconryColorInput = document.getElementById('secondary-color');
      const secondaryColor = seconryColorInput.value;

      ctx.lineWidth = line;

      if (isMouseDown) {
        const canvasWidth = mainCanvas.width;
        const pixelWidth = 640 / canvasWidth;
        const penX = Math.floor(e.offsetX / pixelWidth);
        const penY = Math.floor(e.offsetY / pixelWidth);

        const rectTarget = [];
        let k = 0;
        for (let i = 0; i < line / 7; i += 1) {
          rectTarget.push([i, k]);
          if (i === Math.round(line / 7 - 1) && k < line / 7 - 1) {
            i = -1;
            k += 1;
          }
        }

        for (let i = 0; i < rectTarget.length; i += 1) {
          if (((penX + rectTarget[i][0]) % 2 === 0 && (penY + rectTarget[i][1]) % 2 === 0)
            || ((penX + rectTarget[i][0]) % 2 !== 0 && (penY + rectTarget[i][1]) % 2 !== 0)) {
            ctx.fillStyle = primaryColor;
            ctx.fillRect(penX + rectTarget[i][0], penY + rectTarget[i][1], 1, 1);
          }
          if (((penX + rectTarget[i][0]) % 2 !== 0 && (penY + rectTarget[i][1]) % 2 === 0)
            || ((penX + rectTarget[i][0]) % 2 === 0 && (penY + rectTarget[i][1]) % 2 !== 0)) {
            ctx.fillStyle = secondaryColor;
            ctx.fillRect(penX + rectTarget[i][0], penY + rectTarget[i][1], 1, 1);
          }
        }
      }
    }

    mainCanvas.addEventListener('mousemove', dithDraw);

    mainCanvas.addEventListener('mouseup', () => {
      isMouseDown = false;
      const canvas = document.querySelectorAll('.frame');
      for (let i = 0; i < canvas.length; i += 1) {
        if (canvas[i].classList.contains('selected-frame')) {
          const frames = new FramesColor(mainCanvas, canvas[i]);
        }
      }
    });
  }
}
